.cp-links
{
    $this: &;

    &__link
    {
        &:hover,
        &:focus
        {
            #{$this}__text
            {
                max-width: 9em;
                margin-left: 5px;
            }
        }
    }
    &__text
    {
        transition: 0.3s ease all;
    }
}